import React from 'react'
import kvc_tektel from "./kvc_tektel.png";
import kvc_politer from "./kvc_politer.png";
import daxton from "./daxton.png";
import dopace from "./dopace.png";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { Button } from 'bootstrap';
export default function kvc2() {
    return (
        <div>
            <section id="about" className="about">
                <div className="container">
                    <div className="row">
                        <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                            <div className="mission-stat text-center col-md-8 col-md-offset-2 bottom-space-50">
                                <h2>KVC</h2>

                            </div>
                        </div>
                    </div>
                    <div className="information">
                        <div className="row">
                            <div className="col-xs-12 col-sm-6 col-md-3 col-lg-3 text-center">
                                <img className="pull-center icon" style={{ height: "100px", width: "100px" }} src={kvc_tektel} alt="icon" />
                                <div className="info-col">
                                    <h5>tektel</h5>
                                    <h7>Tanm</h7>
                                    <p className="lead">TEKTEL, steril ISO 5832 Blm-1 "Cerrahi mplantlar iin metalik malzemeler" standardna uygun 316L snf paslanmaz elik telden oluan, emilebilir olmayan steril cerrahi paslanmaz elik tel strdur...</p>
                                </div>
                            </div>
                            <div className="col-xs-12 col-sm-6 col-md-3 col-lg-3 text-center">
                                <img className="pull-center icon" style={{ height: "100px", width: "100px" }} src={kvc_politer} alt="icon" />
                                <div className="info-col">
                                    <h5>politer</h5>
                                    <h7>Tanm</h7>
                                    <p>Polyester filamentinin rlmesinden elde edilen stur kapilaritenin azalmas iin kaplanmtr...</p>
                                </div>
                            </div>
                            <div className="col-xs-12 col-sm-6 col-md-3 col-lg-3 text-center">
                                <img className="pull-center icon" style={{ height: "100px", width: "100px" }} src={dopace} alt="icon" />
                                <div className="info-col">
                                    <h5>dopace</h5>
                                    <h7>Tanm</h7>
                                    <p>DOPACE geici epikradiyal kalp pili veya kalp pili ile izleme iin harici bir pacemaker ile miyokard arasnda iletken bir balant salar ve geici pace sonlandrldktan sonra kolayca karlabilir...</p>
                                </div>
                            </div>
                            <div className="col-xs-12 col-sm-6 col-md-3 col-lg-3 text-center">
                                <img className="pull-center icon" style={{ height: "100px", width: "100px" }} src={daxton} alt="icon" />
                                <div className="info-col">
                                    <h5>daxton</h5>
                                    <h7>Tanm</h7>
                                    <p>daxton-D genel olarak yumuak doku yaknlatrmas ve/veya balanmasnda, kardiyovaskler, cerrahide ve dura mater onarmnda gvenli kullanm salar. daxton-D dokuda minimum inflamasyon reaksiyonuna yol amay amalar...</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row pull-center" >
                        <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                            <div className="mission-stat text-center col-md-8 col-md-offset-2 bottom-space-50">
                                <Link className="btn btn-pink pull-center"
                                    role="button" to={"/kvc"} >Tmn Gster</Link>
                            </div>

                        </div>
                    </div>


                </div></section></div>



    )
}
